.exercise-video {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
  height: 381px;
  text-decoration: none;
  transform: scale(1, 1);
  border-top-left-radius: 20px;
  transition: .3s all ease-in-out;
  overflow: hidden;
  &:hover {
    transform: scale(1.1, 1.1);
  }
}

